import React from 'react'
import { Link } from 'react-router-dom'

import Helmet from '../components/Helmet'
import HeroSlider from '../components/HeroSlider'
import Section, { SectionBody, SectionTitle } from '../components/Section'
import PolicyCard from '../components/PolicyCard'
import Grid from '../components/Grid'
import ProductCard from '../components/ProductCard'

import heroSliderData from '../assets/fake-data/hero-slider'
import policy from '../assets/fake-data/policy'
import productData from '../assets/fake-data/products'
import banner from '../assets/images/banner.png'

const Home = () => {
    return (
        <Helmet title="Trang chủ">
            {/* hero silder */}
                <HeroSlider
                    data={heroSliderData}
                    control={true}
                    auto={true}
                    timeOut={5000}
                />
            {/* end hero silder */}

            {/* Policy section */}
                <Section>
                    <SectionBody>
                        <Grid
                            col={4}
                            mdCol={2}
                            smCol={1}
                            gap={20}
                        >
                            {
                                policy.map((item, index) => (
                                    <Link to="/policy" key={index}>
                                        <PolicyCard 
                                            name={item.name}
                                            description={item.description}
                                            icon={item.icon}
                                        />
                                    </Link>
                                ))
                            }
                        </Grid>
                    </SectionBody>
                </Section>
             {/* end Policy section */}

            {/* best sellings section */}
                <Section>
                    <SectionTitle>
                        Top sản phẩm bán chạy trong tuần 
                    </SectionTitle>
                    <SectionBody>
                        <Grid
                            col={4}
                            mdCol={2}
                            smCol={1}
                            gap={20}
                        >
                            {
                                productData.getProducts(4).map((item, index) => (
                                    <ProductCard
                                        key={index}
                                        img01={item.image01}
                                        img02={item.image02}
                                        name={item.title}
                                        price={item.price}
                                        slug={item.slug}

                                    />
                                ))
                            }
                        </Grid>
                    </SectionBody>
                </Section>
            {/* end best sellings section */}

            {/* new arrival section */}
                <Section>
                    <SectionTitle>
                        Sản phẩm mới 
                    </SectionTitle>
                    <SectionBody>
                        <Grid
                            col={4}
                            mdCol={2}
                            smCol={1}
                            gap={20}
                        >
                            {
                                productData.getProducts(8).map((item, index) => (
                                    <ProductCard
                                        key={index}
                                        img01={item.image01}
                                        img02={item.image02}
                                        name={item.title}
                                        price={item.price}
                                        slug={item.slug}

                                    />
                                ))
                            }
                        </Grid>
                    </SectionBody>
                </Section>
            {/* end new arrival section */}

            {/* banner */}
                <Section>
                    <SectionBody>
                        <Link to="catalog">
                            <img src={banner} alt="" />
                        </Link>
                    </SectionBody>
                </Section>
            {/* end banner */}

            {/* popular products section */}
            <Section>
                    <SectionTitle>
                        Phổ biến
                    </SectionTitle>
                    <SectionBody>
                        <Grid
                            col={4}
                            mdCol={2}
                            smCol={1}
                            gap={20}
                        >
                            {
                                productData.getProducts(12).map((item, index) => (
                                    <ProductCard
                                        key={index}
                                        img01={item.image01}
                                        img02={item.image02}
                                        name={item.title}
                                        price={item.price}
                                        slug={item.slug}

                                    />
                                ))
                            }
                        </Grid>
                    </SectionBody>
                </Section>
            {/* end popular products section */}
        </Helmet>
    )
}

export default Home
